
import * as d3 from 'd3';
import $ from "jquery";
import blah from 'typeahead.js'

// Local imports
import { createCircleChart } from './circle';  // named import
import makeBarChart from './bar';  // name is irrelevant since it is a default export
import Bins from './Bins'

require('./main.scss');  // will build CSS from SASS 3 file

// eslint-disable-next-line no-unused-vars
function buildCircleChart(chartWidth, chartHeight) {
  // Dataset
  const dataset = d3.range(20);

  const svg = d3.select('#viz')
                .append('svg')
                .attr('preserveAspectRatio', 'xMinYMin meet')
                .attr('viewBox', `0 0 ${chartWidth} ${chartHeight}`)
                .classed('svg-content-responsive', true);

  // Test module, string interpolation
  return createCircleChart(svg, dataset);
}

// Main
const vizDiv = document.getElementById("viz");

const chartWidth = vizDiv.clientWidth;
const chartHeight = vizDiv.clientHeight;
// d3.select('#viz').remove();

d3.json("player_matricies.json", function(playerData) {
	d3.json("metric_matricies.json", function(metricData) {
	  const bin = new Bins('viz', playerData, metricData)

	  var substringMatcher = function(strs) {
	    return function findMatches(q, cb) {
	      var matches, substringRegex;

	      // an array that will be populated with substring matches
	      matches = [];

	      // regex used to determine if a string contains the substring `q`
	      substringRegex = new RegExp(q, 'i');


	      // iterate through the pool of strings and for any string that
	      // contains the substring `q`, add it to the `matches` array
	      $.each(strs, function(i, str) {
	      if (substringRegex.test(str)) {
	        matches.push(str);
	      }
	    });
	      cb(matches);
	    };
	  };


	  $('#submit').click(function() {
	    // Get value of input
	    var name = $('input.typeahead.tt-input').val();
	    bin.changePlayer(name)
	  });

	  $('#stat').change(function(e) {
	    bin.changeStatType(e.target.value)
	  })

	  $('.typeahead').typeahead({
	    hint: true,
	    highlight: true,
	    minLength: 1,
	  },
	  {
	    name: 'players',
	    source: substringMatcher(Object.keys(playerData))
	  });
	});
});


